.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 5px 25px;
  width: 375px;
  min-height: 70px;
  margin: 0;

  color: $color-midnight-express;

  background: $main-gradient;
  border: none;
  cursor: pointer;
  border-radius: 50px;

  text-align: center;
  font-size: 35px;
  font-weight: 500;
  line-height: 30px;
  letter-spacing: 0.35px;

  transition: opacity $trans-default;

  &:disabled {
    opacity: 0.5;

    pointer-events: none;
  }

  @include hover-focus {
    opacity: 0.7;
  }

  @include vp-1439 {
    width: 278px;
    min-height: 55px;
    padding: 0 25px;

    font-size: 24px;
    letter-spacing: 0.24px;
  }
}
