.skill-card {
  &__wrapper {
    background-color: $color-picton-blue;
    border-radius: 300px;
    width: 400px;
    border: 1px solid $color-default-black;
    padding: 136px 65px 102px;
    color: $color-default-black;
    text-align: center;
    min-height: 582px;

    @include vp-1439 {
      margin: 0 auto;

      padding: 65px 32px 61px;
      min-height: 417px;
      max-width: 280px;
    }


    @include vp-767 {
      padding: 65px 32px 61px;
      min-height: 417px;
      max-width: 280px;
    }
  }

  &__icon {
    margin-bottom: 50px;

    img {
      margin-left: auto;
      margin-right: auto;
    }
  }

  &__info {
    font-size: 20px;
    font-weight: 600;
    line-height: normal;
    letter-spacing: 0.2px;

    @include vp-1439 {
      font-size: 16px;
      letter-spacing: 0.16px;
    }
  }

  &__text:not(:last-child)  {
    margin-bottom: 24px;
  }


  &__wrapper--square {
    border-radius: 0;

    @include vp-1439 {
      margin: 0 auto;

      padding: 35px 18px 31px;
      min-height: 310px;
      max-width: 280px;
    }

    .skill-card__icon {
      margin-bottom: 25px;
    }

    .skill-card__text:not(:last-child)  {
      margin-bottom: 12px;
    }

    @include vp-1023 {
      display: none;
    }
  }
}
